const selectMortgage = (function () {
  const TEXT_SELECTED = 'Seleccionada';
  const TEXT_SELECT = 'Seleccionar';
  const SELECTOR_MORTGAGE_ITEM = '[data-coronita-layout="mortgage"]';
  const SELECTOR_RADIOBUTTON_CHECK_INPUT = '[data-coronita-c-radiobutton-check] input';
  const SELECTOR_TOGGLE_TEXT_WHITE = '[data-coronita-c-data-amount], [data-coronita-c-link], h4, h5, .flex-order-first>*:not(.m-dialog)';
  const SELECTOR_CONTENT_SKEW = '.mo-stackview__content-skew, .mo-stackview__content-skew--dark';
  const CLASS_TEXT_WHITE = 'text-white';
  const CLASS_C_RADIOBUTTON_DARK = 'c-radiobutton--dark';
  const CLASS_BRAND_PRIMARY = 'bg-brand-primary';
  const CLASS_BRAND_SECUNDARY = 'bg-brand-secundary';

  return function (userData) {
    let data = {};

    function changeRadiobuttonCheck () {
      data.$refs.inputs.forEach((input, index) => {
        parentStyle(index);
      });
    }

    function parentStyle (index) {
      const item = data.$refs.items[index];
      const checked = data.$refs.inputs[index].checked;
      const skewNegativeSmall = item.querySelector('.skew-negative-xsmall');
      item.querySelector('.c-button__text').innerHTML = checked ? TEXT_SELECTED : TEXT_SELECT;
      item.classList.toggle(CLASS_BRAND_SECUNDARY, checked);

      Array.from(item.querySelectorAll(SELECTOR_TOGGLE_TEXT_WHITE)).forEach(itemTextWhite => {
        itemTextWhite.classList.toggle(CLASS_TEXT_WHITE, checked);
      });

      Array.from(item.querySelectorAll('[data-coronita-c-radiobutton]')).forEach(radiobutton => {
        radiobutton.classList.toggle(CLASS_C_RADIOBUTTON_DARK, checked);
      });
      if (skewNegativeSmall) {
        skewNegativeSmall.classList.toggle('hide-content', checked);
      }
      item.querySelector('.flex-order_3').classList.toggle('bg-grey_100', !checked);
      item.querySelector('.flex-order_3').classList.toggle(CLASS_BRAND_PRIMARY, checked);
      const skew = item.querySelector(SELECTOR_CONTENT_SKEW);
      skew.classList.toggle('bg-grey_100', !checked);
      skew.classList.toggle(CLASS_BRAND_PRIMARY, checked);
      skew.classList.toggle('mo-stackview__content-skew', !checked);
      skew.classList.toggle('mo-stackview__content-skew--dark', checked);
    }

    function init () {
      const parent = userData.parent;

      data.$refs = {
        parent: userData.parent,
        items: Array.from(parent.querySelectorAll(SELECTOR_MORTGAGE_ITEM)),
        inputs: Array.from(parent.querySelectorAll(SELECTOR_RADIOBUTTON_CHECK_INPUT))
      };

      changeRadiobuttonCheck();
      data.$refs.inputs.forEach(input => {
        input.addEventListener('change', changeRadiobuttonCheck)
      });
    }

    return {
      init
    };
  };
})();

document.addEventListener('DOMContentLoaded', function () {
  Array.from(document.querySelectorAll('[data-coronita-layout="mortgage-list"]')).forEach(item => {
    let radiobuttonCheck = selectMortgage({
      parent: item
    });
    radiobuttonCheck.init();
  });
});
